.contact-form{
    display: flex;
    margin-top: 40px;
    margin-left: 40px;
    background-color: rgb(255, 255, 255);
    padding: 20px;
    .form{
        width: 40%;
        margin-left: 30%;
    }
}

.contact {
    margin-top:20px;
    display: flex;
    border: solid 1px $text-color;
    padding: 20px;
    width:50%;
    height: 30%;
    .contact-info{
        text-align: left;
    }
}
    .contact-logo {
        margin-top: 20px;

    }
    .name {
        font-size: $font-l;
        margin-bottom: 20px;
    }


.right-part {
    width: 40%;
    position:absolute;
    right:30px;
    .img-contact {
        margin-top: 30px
    }
}

.adress,
.postal,
.city,
.phone,
.mail
{
    color:rgb(54, 54, 54);
    margin-top: 7px;
    margin-bottom: 7px;
}
